import Cookies from 'js-cookie';
import { parseQueryString } from './utils';

const isOmedaId = usr => /^[a-z0-9]{15}$/i.test(usr);

const extractOmedaId = () => {
  const parsed = parseQueryString(window.location.search);
  const usr = parsed['lt.usr'];
  if (!usr) return null;
  return isOmedaId(usr) ? usr : null;
};

const extractExactTargetId = () => {
  const parsed = parseQueryString(window.location.search);
  const usr = parsed['lt.usr'];
  if (!usr) return null;
  if (isOmedaId(usr)) return null;
  const id = parseInt(usr, 10);
  return id || null;
};

const setCookie = (name, userId) => Cookies.set(name, userId, { expires: 365 });

const omedaId = extractOmedaId();
const exactTargetId = extractExactTargetId();

if (omedaId) setCookie('leads-idt', omedaId);
if (exactTargetId) setCookie('et-usr', exactTargetId);
