/**
 * Will invoke the provided function when the DOM is ready.
 * If already ready, the callback will be invoked immediately.
 *
 * @param {Function} callback The ready callback.
 */
export function domReady(callback) {
  if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', function fn() {
      document.removeEventListener('DOMContentLoaded', fn);
      callback();
    });
  } else {
    callback();
  }
}

/**
 * A simple query string parser.
 *
 * @param {string} search
 * @returns {object}
 */
export function parseQueryString(search) {
  if (!search) return {};

  let str = search;
  if (search.indexOf('?') === 0) {
    str = search.replace('?', '');
  }
  return str.split('&')
    .filter(pair => pair)
    .map(pair => pair.split('='))
    .reduce((obj, pair) => {
      const key = decodeURIComponent(pair[0]);
      const value = typeof pair[1] !== 'undefined' ? decodeURIComponent(pair[1]) : undefined;
      return { ...obj, [key]: value };
    }, {});
}
